* {
    font-family: "Muller", Helvetica, sans-serif;
}
* {
    padding: 0;
    margin: 0;
    border: 0;
}
*, :after, :before {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}
body, html {
    height: 100%;
    min-width: 320px;
}
body {
    color: #171717;
    line-height: 1;
    font-family: Muller;
    font-size: 17px;
    -ms-text-size-adjust: 100%;
    -moz-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
button, input, textarea {
    font-family: Muller;
    font-size: inherit;
    outline: 0;
}
button {
    cursor: pointer;
    color: inherit;
    background-color: inherit;
}
a {
    color: inherit;
}
a:link, a:visited {
    text-decoration: none;
}
a:hover {
    text-decoration: none;
}
ul li {
    list-style: none;
}
img {
    vertical-align: top;
}
h1, h2, h3, h4, h5, h6 {
    font-weight: inherit;
    font-size: inherit;
}

.button {
    -webkit-appearance: none;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    width: 100%;
    border: 1px solid #f60;
    color: #f60;
    font-size: 14px;
    line-height: 14px;
    cursor: pointer;
}
.button:hover {
    text-align: center;
    color: #fff;
    background: #f60;
}
.button-regular {
    width: 223px;
    height: 40px;
}
.button-white {
    font-size: 14px;
    line-height: 14px;
    text-align: center;
    color: #f60;
    background: #fff;
}
.button-white:hover {
    border: 1px solid #fff;
}

.container {
    max-width: 1450px;
    width: 100%;
    margin: 0 auto;
    padding: 0 16px;
}

a {
    text-decoration: none;
    color: #171717;
}

.overflow {
    overflow: hidden;
}

.pagination {
    &__wrap {
        margin-top: 100px;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 7px;
        .prev, .next {
            display: none !important;
        }
    }
    a {
        font-weight: 400;
        color: rgba(101, 100, 100, 1);
        font-size: 13px;
        opacity: 1;
        transition: opacity .2s ease-in-out;
        &:hover {
            opacity: 0.8;
        }
    }
    a.active {
        color: #FF6600;
    }
}

.burger {
    display: none;
    position: relative;
    border: none;
    background: none;
    height: 20px;
    width: 30px;
    cursor: pointer;
    z-index: 100;
    margin: 0;
    padding: 0;
}

.burger-line {
    width: 30px;
    height: 2px;
    background-color: #FF6600;
    transition: 0.3s all ease;
}

.top-line {
    position: absolute;
    top: 0;
    right: 0;
}

.mid-line {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translate(0, -50%);
}

.bottom-line {
    position: absolute;
    left: 0;
    bottom: 0;
}

.burger.active > .top-line {
    transform: translateY(8px) rotate(45deg);
    transition: 0.3s all ease;
}

.burger.active > .mid-line {
    opacity: 0;
    transition: 0.3s all ease;
}

.burger.active > .bottom-line {
    transform: translateY(-10px) rotate(-45deg);
    transition: 0.3s all ease;
}

.mobile-menu {
    z-index: 100;
    margin-top: 15px;
    margin-left: 5%;
    padding: 35px 0px 0 20px;
    height: 100vh;
    background-color: #fff;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    display: none;
    flex-direction: column;
    gap: 20px;
    hr {
        width: 100%;
    }
}

.mobile-icon {
    display: flex;
    height: 100%;
    width: 100%;
    justify-content: center;
    align-items: flex-end;
    margin-bottom: 150px;
    gap: 20px;
    a {
        margin: 0;
    }
}

.mobile__nav {
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-items: flex-start;
}

.mobile__link {
    font-size: 18px;
    color: #171717;
    transition: color .2s ease-in-out;
    &:hover {
        color: #FF6600;
    }
}

.mobile__contact {
    gap: 20px;
    flex-wrap: wrap;
    .contact__link {
        font-size: 18px;
        color: #171717;
        &:hover {
            color: #FF6600;
        }
    }
}

.mobile-menu.show {
    display: flex;
}

.footer {
    margin-top: 80px;
    &__content {
        font-weight: 300;
        font-size: 12px;
        line-height: 120%;
        text-align: center;
        color: #000000;
        margin: 0;
        padding: 0;
        padding: 20px 0 70px;
        a {
            opacity: 1;
            transition: opacity .2s ease-in-out;
            &:hover {
                opacity: .9;
            }
        }
    }
}


@media (max-width: 1680px) {
	.container {
		max-width: 1320px;
	}
}

@media (max-width: 1400px) {
	.container {
		max-width: 1140px;
	}
    .pagination__wrap {
        margin-top: 75px;
    }
}

@media (max-width: 1170px) {
	.container {
		max-width: 960px;
	}
}

@media (max-width: 992px) {
	.container {
		max-width: 720px;
	}
    .footer {
        background: url("../img/footer-bg.png") bottom no-repeat;
        background-size: 100% 100%;
        hr {
            display: none;
        }
        &__content {
            font-size: 10px;
            color: #fff;
            padding: 30px 0 40px;
        }
        a {
            color: #fff;
            display: block;
        }
    }
    .pagination {
        &__wrap {
            margin-top: 60px;
            .prev, .next {
                display: block !important;
            }
            .prev {
                width: 10px;
                height: 10px;
            }
            .next {
                width: 21px;
                height: 21px;
            }
        }
    }
}

@media (max-width: 768px) {
	.container {
		max-width: 540px;
    }
}

@media (max-width: 576px) {
	.container {
		max-width: 450px;
	}
}

@media (max-width: 480px) {
    .container {
		box-sizing: border-box;
	}
    .mobile__contact {
        flex-direction: column;
        gap: 10px;
    }
    .footer {
        &__content {
            font-size: 7px;
            padding: 30px 0 20px;
        }
    }
    .pagination__wrap {
        margin-top: 40px;
    }
}