.product-page {
    position: relative;
    background: none;
    .container {
        position: relative;
    }
    &__content {
        margin-top: 40px;
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
    &__sliderWrap, &__block {
        width: 50%;
    }
    &__block {
        max-width: 660px;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        gap: 27px;
    }
    &__status {
        font-size: 11px;
        color: #FF6600;
    }
    &__title {
        margin: 0;
        padding: 0;
        margin-top: 7px;
        font-weight: 700;
        font-size: 27px;
        text-transform: uppercase;
        color: #171717;
    }
    &__articul {
        margin-top: 7px;
        font-weight: 400;
        font-size: 18px;
        line-height: 18px;
        color: #171717;
    }
    hr {
        margin: 0;
        padding: 0;
        width: 100%;
    }
    &__subtitle {
        margin: 0;
        padding: 0;
        font-weight: 400;
        font-size: 20px;
        color: #171717;
    }
    &__descr {
        margin: 0;
        padding: 0;
        margin-top: 3px;
        font-weight: 300;
        font-size: 17px;
        color: #000000;
    }
    &__about {
        white-space: pre-line;
        margin: 0;
        padding: 0;
        font-weight: 300;
        font-size: 15px;
        color: rgba(0, 0, 0, 0.6);
    }
    &__order {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__price {
        font-weight: 400;
        font-size: 30px;
        color: #FF6600;
    }
    &__amount {
        margin-left: 150px;
        display: flex;
            align-items: center;
            gap: 4px;
            button {
                cursor: pointer;
                font-weight: 400;
                font-size: 24px;
                color: #171717;
                padding: 0;
                margin: 0;
                background: transparent;
                border: none;
                outline: none;
            }
            input {
                padding: 0;
                margin: 0;
                height: auto;
                text-align: center;
                width: 35px;
                font-weight: 400;
                font-size: 20px;
                color: #171717;
                border: none;
                outline: none;
            }
    }
    &__btn {
        cursor: pointer;
        border: none;
        background: #FF6600;
        margin: 0;
        padding: 10px 70px;
        font-weight: 400;
        font-size: 14px;
        text-align: center;
        color: #FFFFFF;
        opacity: 1;
        transition: opacity .2s ease-in-out;
        &:hover {
            opacity: .85;
        }
    }
    &__nav {
        margin-top: 130px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    &__slide {
        transform:scale(0.9);
        img {
            object-fit: contai;
            width: 100%;
        }
    }
    &__slider {
        margin-right: 0px;
        padding-top: 120px;
    }
    &__slide.swiper-slide-active {
        z-index: 10 !important;
        transform:scale(1.4);
    }
    &__prev {
        -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
        z-index: 10;
        cursor: pointer;
        width: 8px;
        height: 18px;
    }
    &__next {
        -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
        z-index: 10;
        cursor: pointer;
        width: 32px;
        height: 32px;
    }
    &__pagination {
        max-width: 150px;
        position: static;
        .swiper-pagination-bullet {
            border: 1px solid #656464;
            background: transparent;
        }
        .swiper-pagination-bullet-active {
             border: 1px solid #FF6600;
             background: #FF6600;
        }
    }
    &__logo {
        display: none;
        position: absolute;
        top: 3%;
        left: 12.5%;
        max-width: 124px;
    }
    .breadcrumbs__mobile {
        display: none;
    }
}

.related {
    margin-top: 50px;
    padding: 80px 0;
    background: linear-gradient(90deg, transparent 50%, rgba(228, 227, 232, 0.7) 50%);
    display: flex;
    align-items: center;
    gap: 160px;
    .related__block {
        margin-left: 160px;
    }
    &__wrapper {
        padding-top: 20px;
    }
    &__slide {
        overflow: visible;
    }
    &__title {
        margin-top: 0;
        width: 270px;
        font-size: 27px;
        text-transform: uppercase;
        color: #171717;
    }
    &__nav {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        gap: 20px;
    }
    &__prev {
        -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
        cursor: pointer;
        width: 13px;
        height: 23px;
    }
    &__next {
        -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
        cursor: pointer;
        width: 53px;
        height: 53px;
    }
}


@media (max-width: 1680px) {
	.product {
        &__block {
            max-width: 550px;
        }
        &__nav {
            margin-top: 100px;
        }
    }
    .related {
    padding: 70px 0;
    gap: 120px;
    // overflow: hidden;
    .related__block {
        margin-left: 120px;
    }
    &__title {
        width: 250px;
        font-size: 24px;
    }
}
}

@media (max-width: 1400px) {
	.product-page {
        &__slider {
            padding-top: 100px;
        }
        &__block {
            max-width: 500px;
        }
        &__nav {
            margin-top: 90px;
        }
        &__amount {
            margin-left: 0;
        }
        &__title {
            font-size: 24px;
        }
        &__articul {
            font-size: 14px;
        }
        &__subtitle {
            font-size: 18px;
        }
        &__descr {
            font-size: 16;
        }
        &__about {
            font-size: 14px;
        }
        &__price {
            font-size: 26px;
        }
    }
    .related {
    padding: 70px 0;
    gap: 80px;
    // overflow: hidden;
    .related__block {
        margin-left: 80px;
    }
    &__title {
        width: 240px;
        font-size: 22px;
    }
    &__nav {
        gap: 10px;
    }
    &__prev {
        padding-bottom: 9px;
        width: 10px;
        height: 10px;
    }
    &__next {
        width: 32px;
        height: 32px;
    }
}
}

@media (max-width: 1170px) {
	.product-page {
        &__slider {
            padding-top: 80px;
        }
        &__block {
            max-width: 450px;
        }
        &__nav {
            margin-top: 70px;
        }
    }
}

@media (max-width: 992px) {
	.product-page {
        .breadcrumbs__mobile {
            display: block;
            margin-top: 5px;
        }
        margin-top: 50px;
        &__breadcrumbs {
            display: none;
        }
        &__logo {
            display: block;
        }
        &__content {
            margin-top: 80px;
            flex-direction: column;
        }
        &__sliderWrap {
           width: 75%;
           margin: 0 auto;
        }
        &__slider {
            padding-top: 80px;
        }
        &__block {
            max-width: 720px;
            width: 720px;
            gap: 20px;
        }
        &__nav {
            margin-top: 80px;
        }
        &__status {
            font-size: 10px;
        }
        &__title {
            font-size: 20px;
        }
        &__articul {
            font-size: 12px;
        }
        &__subtitle {
            font-size: 16px;
        }
        &__descr {
            font-size: 14px;
        }
        &__about {
            font-size: 12px;
        }
        &__price {
            font-size: 20px;
        }
        &__prev, &__next {
            display: none;
        }
        &__amount {
           .amount, .plus, .minus {
             font-size: 16px!important;
           }
           .amount {
            width: 30px;
           }
        }
        &__btn {
            font-size: 12px;
            padding: 10px 60px;
        }
    }
    .related {
        padding: 10px 0;
        flex-direction: column;
        align-items: flex-start;
        gap: 20px;
        &__block {
            margin: 0;
            margin-left: 10% !important;
        }
        &__title {
            margin: 0;
            font-size: 18px;
        }
        &__prev, &__next {
            display: none;
        }
        .item__plus {
            display: none;
        }
        &__slider {
            width: 50%;
        }
    }
}

@media (max-width: 768px) {
	.product-page {
        &__block {
            width: 540px;
        }
        &__slider {
            padding-top: 60px;
        }
        &__sliderWrap {
            width: 80%;
        }
        &__logo {
            left: 10%;
        }
        &__nav {
            margin-top: 60px;
        }
    }
}

@media (max-width: 576px) {
	.product-page {
        &__block {
            width: 450px;
        }
    }
}

@media (max-width: 480px) {
    .related {
        &__block {
            padding: 0 16px;
            margin-left: 0 !important;
        }
        &__title {
            font-size: 14px;
            max-width: 140px;
        }
    }
   .product-page {
        margin-top: 40px;
        &__slide.swiper-slide-active {
            transform:scale(1.6);
        }
        &__logo {
            max-width: 112px;
        }
        &__sliderWrap {
           width: 100%;
           margin: 0 auto;
        }
        &__block {
            gap: 15px;
            max-width: 100%;
            width: initial;
            padding: 0 16px 0 16px;
        }
        &__status {
            font-size: 9px;
        }
        &__title {
            font-size: 14px;
        }
        &__articul {
            font-size: 8px;
        }
        &__subtitle {
            font-size: 14px;
        }
        &__descr {
            font-size: 11px;
        }
        &__about {
            font-size: 10px;
        }
        &__price {
            font-size: 16px;
        }
        &__amount {
            gap: 0;
           .amount, .plus, .minus {
             font-size: 14px!important;
           }
           .amount {
            width: 28px;
           }
        }
        &__btn {
            font-size: 11px;
            padding: 10px 50px;
        }
    }
}