// ____________ Модальное окно и форма ________________

.popup {
    background: rgba(0, 0, 0, 0.4);
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    padding: 30px 10px;
    visibility: hidden;
    pointer-events: none;
    &_show {
        z-index: 15000;
        visibility: visible;
        overflow: auto;
        pointer-events: auto;
        transition: visibility .8s ease 0s;
    }
    &_show .popup__content {
        visibility: visible;
        -webkit-transform: scale(1);
        -ms-transform: scale(1);
        transform: scale(1);
    }
    &_hide {
        transition: none;
        visibility: hidden;
    }
    &__wrapper {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -ms-flex-direction: column;
        flex-direction: column;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        min-height: 100%;
        -webkit-box-flex: 1;
        -ms-flex: 1 1 auto;
        flex: 1 1 auto;
        width: 100%;
        display: flex;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        justify-content: center;
        align-items: center;
    }
    &__content {
        background: url(../img/main/popup.jpg) 0 0/cover no-repeat;
        visibility: hidden;
        -webkit-transform: scale(0);
        -ms-transform: scale(0);
        transform: scale(0);
        -webkit-transition: -webkit-transform .3s ease 0s;
        transition: -webkit-transform .3s ease 0s;
        -o-transition: transform .3s ease 0s;
        transition: transform .3s ease 0s;
        transition: transform .3s ease 0s, -webkit-transform .3s ease 0s;
        background-color: #eee;
        padding: 20px;
        // padding: 50px 75px;
        width: 90%;
        max-width: 827px;
    }
    &__close {
        width: 44px;
        height: 44px;
        position: absolute;
        top: -14px;
        right: -14px;
        border-radius: 50%;
        overflow: hidden;
    }
    &__body {
        width: 60%;
        margin: 0 0 0 auto;
    }
    &-price__title {
        font-weight: 700;
        font-size: 27px;
        line-height: 27px;
        color: #171717;
        margin: 0 0 40px 0;
    }
}

.form {
    &__input {
        border-bottom: 1px solid #000;
        margin: 0 0 28px 0;
        input {
            background-color: inherit;
            font-weight: 300;
            font-size: 15px;
            line-height: 15px;
            color: #000;
            padding: 12px;
        }
    }
    &__textarea {
        border: 1px solid #000;
        margin: 0 0 22px 0;
        textarea {
            background-color: inherit;
            font-weight: 300;
            font-size: 15px;
            line-height: 15px;
            color: #000;
            padding: 12px;
        }
    }
    &__bottom {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
    }
    &__btn {
        -webkit-appearance:none;
        border: 1px solid #f60;
        font-size: 14px;
        line-height: 14px;
        text-align: center;
        color: #f60;
